body {
  flex-direction: column;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
  margin: 0;
  font-family: Poppins, sans-serif;
  display: flex;
  overflow-x: hidden;
}

#transactionDiv {
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 40px);
  max-width: 1300px;
  height: max-content;
  margin: 20px;
  display: inline-flex;
}

#titleContainer {
  margin-right: 10px;
}

#title {
  margin: 10px 0;
  font-size: 24px;
  font-weight: 300;
}

#subtitle {
  color: #505050;
  font-size: 14px;
}

#transactionBtn {
  text-wrap: nowrap;
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: none;
  border-radius: 7px;
  width: fit-content;
  padding: 10px;
  font-size: 14px;
  transition: all .5s;
}

#transactionBtn:active {
  background-color: #343434;
  transition: all;
  transform: scale(.9);
}

#resumeTotal {
  grid-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  width: calc(100% - 40px);
  max-width: 1300px;
  display: grid;
}

@media (width <= 900px) {
  #resumeTotal {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width <= 500px) {
  #resumeTotal {
    grid-template-columns: 1fr;
  }
}

.resumeContainer {
  border: 1px solid #00000014;
  border-radius: 12px;
  width: 100%;
  height: 150px;
}

.resumeTop {
  justify-content: space-between;
  align-items: center;
  height: 50%;
  padding: 0 15px;
  font-size: 20px;
  font-weight: 200;
  display: flex;
}

.resumeBottom {
  align-items: center;
  height: 50%;
  padding: 0 15px;
  display: flex;
}

.resumeValue {
  font-size: 28px;
  font-weight: 300;
}

img {
  width: 20px;
}

.resumeIcon {
  align-items: center;
  display: flex;
}

#statsDiv {
  grid-gap: 20px;
  grid-template-columns: repeat(2, 1fr);
  width: calc(100% - 40px);
  max-width: 1300px;
  margin-top: 20px;
  display: grid;
}

#expenses, #quickStats {
  border: 1px solid #00000014;
  border-radius: 12px;
  flex-direction: column;
  height: 350px;
  padding: 20px;
  display: flex;
}

.title {
  font-size: 19px;
  font-weight: 400;
}

#expensesGraph {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

#whenNoGraph {
  color: #505050;
  font-weight: 300;
}

.expenseCalc {
  color: #505050;
  justify-content: space-between;
  margin: 15px 0;
  font-size: 16px;
  display: flex;
}

#quickStats .title {
  margin-bottom: 25px;
}

.result {
  font-weight: 400;
}

#largestAverage {
  color: red;
}

#incomeAverage {
  color: green;
}

#savingsAverage, #expenseAverage {
  color: #000;
}

@media (width <= 900px) {
  #statsDiv {
    grid-template-columns: 1fr;
  }

  #quickStats {
    height: max-content;
  }
}

#recentTransactions {
  border: 1px solid #00000014;
  border-radius: 12px;
  width: calc(100% - 40px);
  max-width: 1300px;
  height: max-content;
  min-height: 250px;
  margin: 20px 0;
}

#recentTransactionsTop {
  justify-content: space-between;
  align-items: center;
  margin: 20px;
  display: flex;
}

#transactions {
  text-wrap: nowrap;
  color: #000;
  cursor: pointer;
  background-color: #00000014;
  border: none;
  border-radius: 7px;
  width: fit-content;
  padding: 10px;
  font-size: 14px;
  transition: all .5s;
}

#noRecentTransactions {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 150px;
  font-weight: 200;
  display: flex;
}

#transactionWindow {
  background-color: #0000;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  transition: all .2s;
  display: none;
  position: absolute;
  transform: scale(0);
}

#container {
  background-color: #fff;
  border-radius: 12px;
  width: 500px;
  margin: 20px;
  padding: 20px;
  transition: all .2s;
  transform: scale(0);
}

#head {
  justify-content: space-between;
  font-size: 18px;
  font-weight: 600;
  display: flex;
}

#closeWindow {
  width: 20px;
  height: 20px;
}

.windowContainer {
  width: 100%;
  margin: 10px 0;
}

.windowTitle {
  font-size: 16px;
  font-weight: 500;
}

.inputWindow {
  color: #797979;
  cursor: pointer;
  background-color: #e3e3e35e;
  border: none;
  border-radius: 12px;
  align-items: center;
  width: calc(100% - 20px);
  height: 40px;
  margin-top: 10px;
  padding: 0 10px;
  font-size: 16px;
  display: flex;
}

#addTransactionWindowBtn {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: none;
  border-radius: 12px;
  width: 100%;
  margin-top: 10px;
  padding: 10px;
  font-size: 16px;
}

#addTransactionWindowBtn:active {
  background-color: #343434;
}

#closeWindow {
  cursor: pointer;
}
